import React from "react";
import { HeadFC, PageProps, graphql } from "gatsby";
import { useLocation } from "@reach/router";

import { IPostPageContext, IPostQueryResult } from "../types/post.type";

import { HeadSeo, LocalBusinessMetadata } from "../components/common/metadata";
import ContentContainer from "../components/common/contentContainer";
import ShareButtons from "../components/posts/shareButtons";
import PostFooter from "../components/posts/postFooter";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import PostHashtags from "../components/posts/postHashtags";

const Post = ({
  data: {
    markdownRemark: {
      frontmatter,
      html,
      wordCount: { words },
    },
  },
  pageContext: { markupData, lang },
}: PageProps<IPostQueryResult, IPostPageContext>) => {
  const { href: currentUrl } = useLocation();

  const { date, title, keywords, author, authorPosition, authorPhoto } =
    frontmatter;

  const avatar = getImage(authorPhoto);

  const minutes = Math.floor(words / 200);
  const readingTime = minutes ? minutes : 1;

  return (
    <>
      <div
        itemScope
        itemType="https://schema.org/Article"
        className="flex bg-main-100"
      >
        <ContentContainer
          maxWidthClass="max-w-2xl"
          className="flex-col text-secondary-100 text-lg md:!px-0 pt-8 sm:pt-16"
        >
          <div
            itemProp="author"
            itemScope
            itemType="https://schema.org/Person"
            className="flex items-center gap-4 mb-8 sm:mb-12"
          >
            {avatar ? (
              <GatsbyImage
                image={avatar}
                alt="avatar"
                itemProp="image"
                className="object-contain aspect-square rounded-full w-16 grayscale"
              />
            ) : null}
            <div className="flex-1 w-max">
              <p itemProp="name" className="font-semibold whitespace-nowrap">
                {author}
              </p>
              <p itemProp="jobTitle" className="text-secondary-30">
                {authorPosition}
              </p>
            </div>
          </div>

          <div className="w-full flex items-center justify-between mb-2.5 sm:mb-5 text-sm sm:text-base">
            <span itemProp="datePublished">{date}</span>

            <span>
              {markupData.readingTimeTitle}: {readingTime}
              {markupData?.readingTimeUnits}.
            </span>
          </div>

          <h2
            className="text-[24px] sm:text-4xl font-semibold mb-8"
            itemProp="headline"
          >
            {title}
          </h2>

          <div
            className="single-post"
            dangerouslySetInnerHTML={{ __html: html }}
            itemProp="articleBody"
          />

          <div className="flex flex-col sm:flex-row w-full gap-8 lg:gap-16 mb-8 md:mb-12">
            {keywords ? (
              <PostHashtags
                keywords={keywords}
                readMoreTitle={markupData.readMoreOnTopicTitle}
              />
            ) : null}

            <ShareButtons
              url={currentUrl}
              postTitle={title}
              shareTitle={markupData.sharePostTitle}
            />
          </div>

          <PostFooter
            title={markupData.postFooterTitle}
            text={markupData.postFooterText}
          />
          {/* <div className="w-full flex flex-col md:flex-row justify-between items-center pb-24">
            {keywords ? (
              <div className="basis-full sm:basis-3/5 text-center md:text-start pb-10 md:pb-0">
                {keywords.map((hashtag) => (
                  <div
                    key={hashtag}
                    className="inline-block text-main-10 border-main-10 border-solid border rounded-full p-1 px-4 text-2xl m-2"
                    itemProp="keywords"
                  >
                    #{hashtag.toLowerCase()}
                  </div>
                ))}
              </div>
            ) : null}
            <div className="basis-full sm:basis-2/5 flex flex-row justify-end">
              <ShareButtons url={currentUrl} title={title} />
            </div>
          </div> */}
        </ContentContainer>
        <LocalBusinessMetadata itemProp="copyrightHolder" lang={lang} />
      </div>
    </>
  );
};

export default Post;

export const Head: HeadFC<IPostQueryResult, IPostPageContext> = ({
  data: {
    markdownRemark: {
      frontmatter: { title },
    },
  },
  pageContext,
  location,
}) => (
  <HeadSeo
    title={`${pageContext.markupData.head} | ${title.toUpperCase()}`}
    siteUrl={pageContext.site.siteUrl}
    path={location.pathname}
    lang={pageContext.lang}
  />
);

export const query = graphql`
  query ($id: String, $lang: String) {
    markdownRemark(id: { eq: $id }) {
      frontmatter {
        date(formatString: "DD MMM YYYY", locale: $lang)
        title
        keywords
        author
        authorPosition
        authorPhoto {
          childImageSharp {
            gatsbyImageData(
              width: 64
              formats: [AUTO, WEBP, AVIF]
              placeholder: BLURRED
            )
          }
        }
      }
      html
      wordCount {
        words
      }
    }
  }
`;
